import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Mycontext } from '../App';
import Title from './Title'

function Setting() {

    const navigate = useNavigate();
    const {toggleMode, setLine,line} = useContext(Mycontext)

    return <div className='container'>
        <div className='row justify-content-center align-items-center vh-100 text-white'>
            <div className='col-12 text-end close-button'><button className='btn btn-primary fs-4' onClick={() => navigate('/home')}>X</button></div>
            <Title prop={{name: "Setting", text: "More fix and tools !"}}/>
            <div className='col-md-6 col-sm-12 theme'>
                <button className='btn' onClick={toggleMode}>
                    <i className={`fa-solid fa-sun`}> Theme</i>
                </button>
            </div>
            <div className='col-md-6 col-sm-12'><input type='color' defaultValue={line} onChange={(e)=> setLine(e.target.value)} className='w-100'/></div>
            <div className='col-md-6 col-sm-12'>
                <button className='btn btn-primary w-100 p-3 round' onClick={()=> {localStorage.clear('user'); navigate('/login')}}>Log out</button>
            </div>
        </div>
    </div>
}

export default Setting