import React, { useEffect, useState } from 'react'
import Way from '../config';

function Carusel() {

    const [carusel, setCarusel] = useState([]);

    useEffect(() => {
        Way.get("index.json")
        .then(res => res.data.forEach(a => setCarusel(a)))
        .catch(r=>r);
    }, []);

    return <div id="carouselExampleCaptions" className="carousel slide mt-5" data-bs-ride="carousel">
        <div className="carousel-indicators">
            <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
            <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1" aria-label="Slide 2"></button>
            <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="2" aria-label="Slide 3"></button>
        </div>
        <div className="carousel-inner">
            {
                carusel.map((a, b) => <div key={b} className="carousel-item active">
                    <img src={"https://raw.githubusercontent.com/HelloAbdulloh/br1img/main/" + a.img} className="d-block w-100" />
                    <div className="carousel-caption">
                        <div>
                            <h5>{a.title}</h5>
                            <p>{a.describe}</p>
                        </div>
                    </div>
                </div>)
            }
        </div>
        <button className="carousel-control-prev c-btn" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
            ⋘
        </button>
        <button className="carousel-control-next c-btn" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
        ⋙
        </button>
    </div>
}

export default Carusel