import React, { useEffect, useState } from 'react'
import Title from './Title'
import Way from '../config';

function Aksesuares() {

    const [akse,setFilters] = useState([]);
    const [thisPage, setThisPage] = useState(0);

    useEffect(()=>{
        Way.get('filter.json')
        .then(res => setFilters(res.data))
        .catch(r => r);
    },[])

    return <div className='container'>
        <div className='row'>
            <Title prop={{name: "Aksesuarlar (shop)", text: "Bu bo'limda siz o'zingizga kerak aksesuarlarni topasiz"}}/>
            {
                akse[thisPage]?.map((a,b) => <div key={b} className='col-md-2 col-sm-2 mt-3'>
                <div className="card bg-transparent">
                        <div className="card-body color-card">
                            <h5 className="card-title">{a.name}</h5>
                            <p>{(+a.price * 15).toFixed()} 000 so'm</p>
                            <button className='btn btn-primary'>More read</button>
                        </div>
                </div>
            </div>)
            }
        </div>
        <div className='row mt-3 mb-3'>
        <div className='col-4'><button className='btn btn-primary w-100' onClick={() => setThisPage(prev => prev > 0 ? prev - 1 : prev)}>Prev</button></div>
            <div className='col-4 fs-4 about text-center'>{thisPage+1}</div>
            <div className='col-4'><button className='btn btn-primary w-100' onClick={() => setThisPage(prev => prev < akse.length-1 ? prev + 1 : prev)}>Next</button></div>
        </div>
    </div>
}

export default Aksesuares