import React, { useContext, useEffect, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Mycontext } from '../App';

function LoginIn() {

  const navigate = useNavigate();

  const {line,setLine,Submit} = useContext(Mycontext);

  return <div className='container vh-100 d-flex justify-content-center align-items-center'>
    <form onSubmit={(e)=> Submit(e)} className='p-5 rounded login-in'>
      <div className='login-text'>
        <h1>Ro'yhatdan <span className='danger'>
        o'tish </span></h1>
        <p>Akvarium olami ro'yxatdan o'tish. <br/>
          Eslatib o'tamiz bu hech qanday malumotlarni o'girlash yoki hakkerlik hujumi emas !
        </p>
      </div>
      <input type='text' className='form-control mb-4' required placeholder='Ismingiz...' />
      <input type='tel' className='form-control mb-4' required placeholder='Telefon raqamingiz...' />
      <input type='color' defaultValue={line} onChange={(e)=> setLine(e.target.value)} className='w-100'/>
      <label className='mb-4'>Yoqtirgan rangingiz...</label>
      <button className='btn btn-primary text-dark w-100' type="submit">Login</button>
    </form> 
  </div>
}

export default LoginIn