import React from 'react'
import Title from './Title';

function Cattegory() {
    return <div className='container'>
        <div className='row'>
            <Title prop={{name: "Kategoriya", text: "Bu bo'limda siz o'zingizga yoqgan turdagi akvariumlarni tanlaysiz"}}/>
            <div className='col-md-3 col-sm-12 mb-3'>
                <img src='https://www.imgcorporations.com/images/bg-img.jpg' className='w-100 rounded'/>
            </div>
        </div>
    </div>
}

export default Cattegory