import React, { useEffect, useState } from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom';
import Social from './Social';

function Navbar() {
  
  const navigate = useNavigate()

  return <nav className="navbar sticky navbar-expand-lg">
    <div className="container-fluid">
      <a className="navbar-brand" href="#">Akvarium <span className='danger'>olami</span></a>
      <button className="navbar-toggler border-0" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="fa-solid fa-bars-staggered affect"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav m-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <a className="nav-link active" aria-current="page" href="#">Home</a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://t.me/akvarium_olami">Telegram</a>
            </li>
          <li className="nav-item">
            <a className="nav-link">Projects</a>
          </li>
          <li className="nav-item">
            <NavLink className={'nav-link'} to={'/aksesuarlar'}>Aksesuar's</NavLink>
            </li>
          <li className="nav-item">
            <a className="nav-link" onClick={()=> navigate('/setting')}>Settings</a>
          </li>
        </ul>
       <Social/>
      </div>
    </div>
  </nav>
}

export default Navbar